import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Auditoria } from '../../../modelo/Auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'auditoria-dados-details',   templateUrl: 'auditoria_dadosDetails.component.html' }) export class AuditoriaDadosDetailsComponent implements OnInit {    auditoria: Auditoria;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.auditoria.Auditoria";                 let id = +params['id'];         this.auditoria = new Auditoria();         this.auditoria.cd_auditoria = id;         o.objetoJson = this.auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a auditoria. '; }      this.auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


