import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'auditoria-dados',   templateUrl: 'auditoria_dados.component.html' }) export class AuditoriaDadosComponent implements OnInit {       displayMode: AuditoriaDadosDisplayModeEnum;     displayModeEnum = AuditoriaDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = AuditoriaDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = AuditoriaDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = AuditoriaDadosDisplayModeEnum.Edit;           break;       }     }  }  enum AuditoriaDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


